.profile-view {
  position: absolute;
  margin: 0;
  padding: 0 !important;
  width: 100%;
}

.form-container-profile {
  background: black;
  color: white;
  box-shadow: 0px 0px 18px 1px white;
  padding: 30px 60px 60px 60px;
  width: 400px;
  min-height: 625px;
  max-height: 770px;
  font-size: 1.2em;
  margin: 30px 0 0 0;
  text-align: left;
}

.btn-profile {
  display: block;
  width: 100%;
  margin-top: 15px;
  margin-right: 15px;
}

.favorite-movies-list {
  text-align: left;
  width: 405px;
}

.favorite-movies-list ul li {
  list-style-type: none;
  margin-top: 1em;
}

.favorite-movies-list ul {
  padding-left: 0;
}

.user-info-text {
  text-align: left;
}

.user-info-item {
  color: #b8b8b8;
  line-height: 2;
}

span {
  font-weight: bold;
  color: white;
}